.App {
    max-width: 1084px;
    padding: 0 25px;
    margin: 0 auto;
}

.users {
    display: flex;
    padding: 10px 12px;
    background-color: aliceblue;
    color: red;
    display: block;
    width: 68%;
    font-size: 25px;
    margin-bottom: 10px;
    border-radius: 10px;
}

.name {
    font-weight: 700;
    color: rgb(153, 155, 221);
}

.street {
    color: rgb(141, 46, 106);
}

.text {
    color: rgb(15, 10, 74);
}

.wrap {
    padding: 20px 15px;
    background-color: rgb(195, 196, 234);
    width: 73%;
    margin: 0 auto;
    border-radius: 20px;
}

.inner-wrap {
    color: rgb(255, 255, 255);
    width: 60%;
    display: flex;
    justify-content: space-between;
}

.posts {
    width: 100%;
    background-color: blue;
    font-size: 25px;
    margin-bottom: 10px;
    padding: 12px 8px;
    border-radius: 5px;
}

.tc {
    text-align: center;
}