.edit {
    color: #fff;
    background-color: rgb(189, 189, 241);
    border: 0;
    border-radius: 5px;
    padding: 10px 20px;
    font-size: 17px;
    cursor: pointer;
}

.edit:active {
    background-color: aqua;
}

.delete {
    color: blanchedalmond;
    background-color: #f00;
    border: 0;
    border-radius: 5px;
    padding: 10px 20px;
    font-size: 17px;
    cursor: pointer;
}

.texts {
    font-size: 22px;
    margin: 0 20px;
}

.checkbox {
    width: 20px;
    height: 20px;
    border-radius: 50%;
}

.text-done {
    text-decoration: line-through;
}