.wrapp {
    display: flex;
    align-items: center;
}

.red {
    background-color: red;
    border: 0;
    border-radius: 5px;
    color: #fff;
    padding: 10px 22px;
    font-size: 18px;
    cursor: pointer;
}

body {
    color: #ffffff;
    background-color: rgb(29, 4, 95);
}

a {
    text-decoration: none;
    color: rgb(255, 255, 255);
}

.nav {
    display: flex;
    justify-content: space-between;
    width: 700px;
    margin-left: 50px;
    padding: 10px 22px;
    background-color: rgb(113, 118, 180);
    border-radius: 8px;
}

.circle2 {
    border-radius: 50%;
}

.links {
    background-color: rgb(29, 68, 186);
    border-radius: 5px;
    padding-left: 20px;
    padding-right: 20px;
}

.list-wrap {
    margin-top: 15px;
    max-width: 200px;
    display: block;
    padding: 15px 10px;
    background-color: #fff;
    color: blue;
    font-size: 18px;
    font-weight: 700;
    border-radius: 25px;
}

.list {
    list-style-type: none;
}

.item {
    display: block;
    text-decoration: none;
    color: gray;
    font-weight: 600;
    margin-bottom: 10px;
}

.item2 {
    display: block;
    margin-bottom: 10px;
    padding: 5px 5px;
    background-color: blueviolet;
    border-radius: 5px;
}